* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
ul {
	list-style: none;
}

html, body {
	height: 100%;
}

#root {
	min-height: 100%;
	min-width: 100%;
	background-color: #000;

	display: grid;
	grid-template-columns: 1fr minmax(auto, 1200px) 1fr;
}

.container {
	height: 100%;
	background-color: #7d7b75;
	grid-column: 2;

	display: grid;
	grid-template-rows: minmax(auto, 70px) 1fr;
}

.header {
	background-color: #333333;
	border: 2px solid #212529;	
	border-radius: 25px 25px 55px 5px/5px 55px 25px 25px;
	margin: 0 20px;
	padding: 0 20px;

	display: flex;
}

.header__logo {
	display: flex;
	align-items: center;
}
.header__logo-text {
	color: #fff;
	font-family: Cabin Sketch;
	font-size: 22px;
	font-weight: 400;
	text-decoration: none;
	color: #fff;
}

.header__nav {
	display: grid;
	grid-template-columns: minmax(400px, auto);
	align-items: center;
}

.header__list {
	color: #fff;
	font-weight: 700;

	display: grid;
	grid-auto-flow: column;
	justify-content: start;
}

.header__list li {
	padding: 0 40px;
}

.header__item {
	cursor: pointer;
	color: #fff;
	text-decoration: none;
}

.main__header {
	display: flex;
	justify-content: space-between;
	margin: 0 20px;
	padding: 5px;
	width: 100px;
}
.main__header button {
	background-color: #333;
	color: #fff;
	border: 1px solid #212529;
	border-radius: 25px 25px 55px 5px/5px 55px 25px 25px;
	padding: 3px 7px;
	margin: 0 5px;
}
.btn-toggle-list {
	width: 50px;
}
.control-font-size {
	display: none;
}
.control-animation {
	display: none;
}
.btn-change-thema {
	display: none;
}
.main__body {
	display: flex;
	align-items: flex-start;
}

.aside {
	background-color: #fff;
	transition: all 0.3s linear;
	border: 0 solid #333;
	border-top: 2px solid #333;
	border-bottom: 2px solid #333;
	border-radius: 45px 15px 35px 5px/15px 5px 15px 65px;
	width: 0;
	overflow: hidden;
}

.aside.active {
	margin-left: 20px;
	border: 2px solid #333;
	width: 120px;
	flex: 0 0 auto;
}

.aside__list {
	flex-direction: column;
	gap: 5px;
	padding: 5px;
	transition: all 0.3s linear;
	display: flex;
}

.aside__list li {
	background-color: #333;
	padding: 10px;
	border-top: 2px solid #333;
	border-bottom: 2px solid #333;
	border-radius: 255px 5px 225px 5px/25px 225px 25px 255px;
	cursor: pointer;
	flex: 0 0 50px;
	width: 106px;
}

.aside__item {
	font-size: 14px;
	font-weight: 700;
	color: #fff;
	transition: all 0.2s linear;
	display: block;
}

.content {
	background-color: #fff;
	flex: 1 1 auto;
	padding: 5px;
	margin: 0 20px;
	border: 2px solid #333;
	border-radius: 5px 5px 5px 5px/25px 25px 25px 5px;
	transition: all 0.3s linear;
	display: none;
}

@media(max-width: 767px) {
	.content {
		overflow: auto;
		background-color: rebeccapurple;
	}
}

.content.active {
	display: block;
}

.btn-menu {
	padding: 0 10px;
}